.MenuCard {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 15px 30px;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 4px 8px 0 rgba(88, 84, 132, 0.4);
  transition: 0.3s;

  &:hover {
    box-shadow: 0 8px 16px 0 rgba(88, 84, 132, 0.4);
  }

  .MenuCard-img {
    width: 194px;
    height: 194px;
  }

  .MenuCard-text {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 5px;
  }

  .MenuCard-price {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 5px;
  }

  .MenuCard-ingredients {
    margin-bottom: 5px;
  }

  .MenuCard-ingredients {
    font-size: 12px;
    font-style: italic;
    color: gray;
  }

  .Qty-Btn {
    width: 100%;

    > button {
      color: #3b2f7d;
      border-color: #3b2f7d;
      font-weight: 600;
      border-radius: 15px;

      &:hover {
        color: white;
        border-color: #3b2f7d;
        background-color: #3b2f7d;
      }
    }
  }

  .MenuCard-qty {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    font-weight: 500;

    > input {
      display: block;
      width: 50%;
      height: 100%;
      padding: 5px 10px;
      font-size: 1rem;
      background-clip: padding-box;
      border: 1px solid #3b2f7d;
      border-radius: 15px;
      transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
      outline: none;

      &:active {
        border-radius: 15px;
      }
    }
  }
}
